@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap";
:root {
  --blue-color: #00acdc;
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

body {
  background-color: #fff;
  margin: 0 auto;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
}

header {
  justify-content: space-between;
  align-items: center;
  width: 1024px;
  padding: 0 50px;
  display: flex;
}

@media (width <= 1199px) {
  header {
    justify-content: space-between;
    width: 1024px;
  }
}

@media (width >= 1200px) {
  header {
    width: 1200px;
  }
}

ul {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

li {
  text-transform: uppercase;
  position: relative;
}

a {
  text-align: center;
  color: #000;
  height: 100%;
  padding: 23px 0;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

a.is-active {
  color: var(--blue-color);
  transform: scaleX(1);
}

a:after {
  content: " ";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  transition: transform .3s;
  position: absolute;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

a.is-active:after {
  transform: scaleX(1);
}

li:hover a, li:focus a {
  color: var(--blue-color);
}

ul li:not(:last-child) {
  margin-right: 20px;
}

.logo-link {
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}
